<template>
  <div id="logo">
    <div style="display: flex;">
      <button @click="copyToClipboard" :class="{ 'copied': isCopied }" style="margin-left: 50px;">复制优惠码</button>
      <textarea v-model="text" rows="4" cols="50"
        style="font-size: 12px; resize: none; width: 300px; height: 40px;"></textarea>
    </div>
  </div>
  <HuaShu></HuaShu>
</template>

<script>
import HuaShu from '@/views/HuaShu.vue'

export default {
  name: 'App',
  components: {
    HuaShu,
  },
  data() {
    return {
      text: '', // 存储 textarea 的内容
      isCopied: false
    };
  },
  methods: {
    async clearCache() {
      try {
        // 清除 Cache API 的缓存
        await caches.delete('image-cache');

        console.log('缓存已清除');
      } catch (error) {
        console.error('清除缓存失败：', error);
      }
    },
    copyToClipboard() {
      // 创建一个临时的 textarea 元素来进行复制
      const textArea = document.createElement('textarea');
      textArea.value = this.text;
      document.body.appendChild(textArea);
      textArea.select();
      document.execCommand('copy');
      document.body.removeChild(textArea);

      // 复制成功后，改变按钮背景颜色
      this.isCopied = true;

      // 2 秒后恢复按钮原始颜色
      setTimeout(() => {
        this.isCopied = false;
      }, 500);
    }
  }
}
</script>


<style>
#clear {
  padding: 5px 10px;
  font-size: 15px;
}

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 20px;
}

button {
  box-sizing: border-box;
  border: 1px solid black ;
}

button.copied {
  background-color: red;
  color: white;
  border: 1px solid black ;
}
</style>