<template>
  <div id="hello">
    <div v-if="loading" class="progress-bar-container">
      <span style="position: absolute;">图片加载中...</span>
      <div class="progress-bar" :style="{ width: loadProgress + '%' }"></div>
    </div>

    <!-- 获取全部数组 -->
    <ul v-for="(arr, index) in allItems" :key="index">
      <!-- 主标题 -->
      <div class="title"><span>{{ titles[index] }}</span></div>
      <li class="box" v-for="(item, index2) in arr" :key="index2" @click="copyImageOrTextToClipboard(item, $event)">
        <!-- 发票点击链接 -->
        <a v-if="item.title == '开发票'" class="fapiao" href="http://fp.imwayson.com" target="_blank"
          @click.stop="copyImageOrTextToClipboard()">{{ fapiao }}</a>
        <!-- 副标题 -->
        <span>{{ item.title }}</span>
        <!-- 如果存在图片就复制图片 -->
        <img v-if="item.img" :src="item.img" @load="handleImageLoadAndError(true)" @error="handleImageLoadAndError(false)"
          style="display:none;" />
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>
    <div style="clear: both;height: 30px"></div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  data() {
    return {
      fapiao: ">>> 点击开发票 <<<",
      titles: ['日常使用', '软件问题', 'iOS & iPad', 'Windows', 'macOS', 'Android'],
      loading: true, // 加载进度条状态
      loadProgress: 0, // 已加载图片的进度
      totalImages: 0, // 要加载的图片总数
      loadedImages: 0, // 已经加载的图片数
      isCopying: false // 是否正在复制
    };
  },
  computed: {
    // 获取全部话术
    ...mapGetters(['allItems']),
  },
  created() {
    // 初始化 totalImages
    this.totalImages = this.allItems.flat().filter(item => item.img).length;
    // 页面加载时自动缓存图片
    this.cacheAllImages();
  },
  mounted() {
    console.log("页面已加载...");
  },
  methods: {
    // 缓存所有图片
    async cacheAllImages() {
      for (const item of this.allItems.flat()) {
        if (item.img) {
          await this.cacheImage(item.img);
        }
      }
    },
    // 缓存单个图片
    async cacheImage(url) {
      if (!('caches' in window)) {
        console.error('当前浏览器不支持 Cache API');
        return null;
      }

      try {
        const cache = await caches.open('image-cache');
        const response = await fetch(url);
        if (response.ok) {
          await cache.put(url, response.clone());
          console.log(`图片已缓存: ${url}`);
          return response.blob();  // 返回 blob 数据
        } else {
          throw new Error('网络请求失败');
        }
      } catch (error) {
        console.error('缓存图片失败:', error);
        return null;
      }
    },
    // 获取图片的 Blob
    async getImageBlob(url) {
      if (!('caches' in window)) {
        console.error('当前浏览器不支持 Cache API');
        return null;
      }

      const cache = await caches.open('image-cache');
      const cachedResponse = await cache.match(url);

      if (cachedResponse) {
        console.log(`从缓存中读取图片: ${url}`);
        return cachedResponse.blob();
      }

      console.log(`缓存中没有图片，开始下载并缓存: ${url}`);
      return await this.cacheImage(url);  // 返回从下载并缓存的图片
    },
    // 处理图片加载
    handleImageLoadAndError(bool) {
      if (bool) {
        console.log('图片加载成功')
        this.handleImageFunc()
      } else {
        console.error('图片加载错误');
        this.handleImageFunc()
      }
    },
    handleImageFunc() {
      this.loadedImages++;
      this.loadProgress = (this.loadedImages / this.totalImages) * 100;
      if (this.loadedImages === this.totalImages) {
        this.loading = false;
      }
    },
    // 复制文本到剪贴板
    async copyToClipboard(data) {
      try {
        // 检查浏览器是否支持 Clipboard API
        if (!navigator.clipboard) {
          console.error('当前浏览器不支持 Clipboard API');
          return;
        }
        // 使用 Clipboard API 将数据写入剪贴板
        await navigator.clipboard.writeText(data);
        console.log('内容已成功复制到剪贴板');
      } catch (err) {
        // 如果发生错误，捕获并在控制台输出错误信息
        console.error('复制到剪贴板失败：', err);
      }
    },
    // 复制图片或文本到剪贴板
    async copyImageOrTextToClipboard(item, event) {
      // 是否在复制中?
      if (this.isCopying) {
        return;
      }
      this.isCopying = true;

      try {
        if (item.content === '') {
          if (item.img === '') {
            console.error('无可复制内容');
            return;
          }

          const imgBlob = await this.getImageBlob(item.img);
          if (!imgBlob) {
            console.error('无法获取图片');
            return;
          }

          const clipboardItemInput = new ClipboardItem({ 'image/png': imgBlob });
          await navigator.clipboard.write([clipboardItemInput]);
          console.log('图片已成功复制到剪贴板');
        } else {
          await this.copyToClipboard(item.content);
        }

        const clickedElement = event.target;
        clickedElement.style.backgroundColor = 'red';
        setTimeout(() => {
          clickedElement.style.backgroundColor = '';
        }, 500);
      } catch (err) {
        console.error('复制到剪贴板失败：', err);
      } finally {
        setTimeout(() => {
          this.isCopying = false;
        }, 500);
      }
    }
  }
}
</script>



